.navbar,.footer{position:fixed;left:0;width:100%;padding:0.75rem 2rem;background:#141414;z-index:1;display:flex;justify-content:space-between}.navbar{top:0;height:7vh;align-items:center}.navlink{margin:0 5px;color:#b1b4ba;font-size:1.5rem}.nav-links-lg-screen{align-self:center;list-style:none}.nav-links-sm-screen{position:fixed;top:0;left:0;width:100%;height:0;overflow:hidden;margin-top:7vh;display:flex;flex-direction:column;justify-content:center;background-color:#141414;opacity:0.95;transition:all 0.3s linear}.nav-sm-screen{display:none}.show-nav{height:87vh}.navlink-sm-screen{height:30%;text-align:center;transition:all 0.3s linear;font-weight:700;letter-spacing:3px;text-transform:uppercase;display:flex;justify-content:center;align-items:center}.footer{bottom:0;height:6vh}a{text-decoration:none;margin:0 10px}.main-section{height:87vh;margin-top:7vh;margin-bottom:6vh;background-image:url(/static/media/mevwzxctulj61.9893c8e2.jpg);background-position:center;object-fit:cover;display:flex;justify-content:center;align-items:center}.hero-section{display:flex;justify-content:center;align-items:center;height:100%}.hero-content-container{width:75%;min-width:265px;max-width:550px;max-height:90vh;background-color:#141414;border-radius:10px;display:flex;flex-direction:column;justify-content:center;align-items:center}.hero-description{padding:1rem 1.5rem;margin:0.5em;text-align:center;font-size:1.2rem}.hero-title{text-align:center;padding:1.5rem 0 0 0;margin-bottom:0.5em}.hero-tech-stack-section{display:grid;grid-template-columns:repeat(auto-fill, minmax(90px, 1fr));justify-content:center;width:80%;padding-bottom:2rem}.tech-stack-item{text-align:center;margin:0.3em}.tech-stack-item-subtext{font-size:.8rem;margin-top:0.3em}.button,.button-outline,.button-filled{color:#b1b4ba;font-family:"Open Sans",sans-serif;font-size:1.2rem;cursor:pointer;outline:none;transition:color 250ms}.button:hover,.button:focus,.button-outline:hover,.button-outline:focus,.button-filled:hover,.button-fil